<template>
    <div id="addBlog">
        <form v-if="!submitted">
            <!-- required -- 必须要填内容 -->
            <label>博客标题:</label>
            <input type="text" required v-model="blog.title">
            
            <label>博客内容:</label>
            <textarea v-model="blog.content"></textarea>
            <div id="checkedboxes">
                <label>Vue.js</label>
                <input type="checkbox" value="vue.js" v-model="blog.categories">
                <label>Node.js</label>
                <input type="checkbox" value="Node.js" v-model="blog.categories">
                <label>React.js</label>
                <input type="checkbox" value="React.js" v-model="blog.categories">
                <label>Angular4</label>
                <input type="checkbox" value="Angular4" v-model="blog.categories">
            </div>
            <label>作者:</label>
            <select v-model="blog.author">
                <option v-for="author in authors">{{author}}</option>
            </select>
            <button @click.prevent="submited">添加博客</button>
        </form>

        <hr>
        <div v-if="submitted">
            <h3>您的博客发布成功!</h3>
        </div>
        <div id="preview">
            <p>博客总览</p>
            <p>博客标题：{{blog.title}}</p>
            <p>博客内容: {{blog.content}}</p>
            <p>博客类型: {{blog.categories}}</p>
            <ul>
                <li v-for="category in blog.categories" :key="category">{{category}}</li>
            </ul>
            <p>作者:{{blog.author}}</p>
        </div>
    </div>
</template>
<script>
export default {
    name : "addBlog",
    // created () {
    //     this.blog.author = "Sunny";
    // },
    data () {
        return {
            blog : {
                title : "",
                content : "",
                categories : [],
                author : ""
            },
            authors : ["Sunny","Cherry","Henry"],
            submitted : false,
        }
    },
    methods : {
        submited () {
            // 要把这些提交的博客信息提交到自己的数据库里面，这里提交到自己的野狗云里面，要到com后面加上自己定义的一个xx.json的文件，这样的话，野狗云数据库会自己创建这个文件，并且把提交的信息放到这个json文件里面
            this.$http.post("https://wd5316696857vhtgfi.wilddogio.com/posts.json",this.blog).then( (response) => {
                console.log(response);
                this.submitted = true;
            })
        }
    }
    
}
</script>
<style scoped>
#addblog *{
    box-sizing: border-box;
}
#addblog{
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
}
label{
    display: block;
    margin:20px 0 10px;
}
input[type="text"],textarea,select{
    display: block;
    width:300px;
    padding:8px;
}
textarea{
    height:200px;
}
#checkedboxes label{
    display: inline-block;
    margin-top:0;
}
#checkedboxes input{
    display: inline-block;
    margin-right:10px;
}
button{
    display: block;
    margin:20px 0;
    background:sandybrown;
    color:seashell;
    border:0;
    padding:14px;
    border-radius: 4px;
    font-size: 10px;
    cursor:pointer;
}
#preview{
    padding:10px 20px;
    border:1px dotted #ccc;
    margin:30px 0;
}
h3{
    margin-top:10px;
}
</style>




