<template>
    <div class="single-blog">
        <h1>博客标题:{{blog.title}}</h1>
        <article>
            博客内容:{{blog.content}}
        </article>
        <p>博客作者:{{blog.author}}</p>
        <p v-for="category in blog.categories" :key="category">博客类型:{{category}}</p>
    </div>
</template>
<script>
export default {
    name : "single-blog",
    data () {
        return {
            id : this.$route.params.id,
            blog : {}
        }
    },
    created () {
        this.$http.get('https://wd5316696857vhtgfi.wilddogio.com/posts/' + this.id + '.json').then( (response) => {
            // this.blog = response.body
            return response.json();
        }).then(function (data) {
            console.log(data);
            this.blog = data;
        })
    }
}
</script>
<style scoped>
.single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background:#ccc;
    border: 1px dotted #aaa;
}

</style>




