// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'


Vue.config.productionTip = false
Vue.use(VueResource);

// 全局自定义指令  有一个对象，vue提供了5个钩子函数，每个钩子函数里面有4个参数，其中参数bingding对象里面又有5个属性
// 1.改变背景颜色，并且通过传进来的宽窄改变宽度
Vue.directive('theme',{
  bind(el,binding,vnode) {
    if(binding.value == 'wide') {
      // v-theme:column = 'wide' 拿到wide -> binding.value --> wide
      el.style.maxWidth = '1260px';
    }else if(binding.value == 'narrow') {
      el.style.maxWidth = '560px';
    }
    if(binding.arg == 'column') {
        //类似于 v-on:click
       // v-theme:column = "wide"  拿到column -> bingding.arg -> column
       el.style.background = "skyblue";
       el.style.padding = "20px";
    }
  }
})

// 全局自定义过滤器(过滤方法自己写)
// 1.把截取content的长度
Vue.filter("snippet",function (value) {
    return value.slice(0,77) + "...";
})


new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



