<template>
    <div id="show-blogs" v-theme:column="'wide'">
        <h1>博客总览</h1>
        <input type="text" placeholder="搜索" v-model="search">
        <div class="single-blog" v-for="blog in filteredBlogs" :key="blog.id">
            <router-link :to="/blog/ + blog.id">
            <h3 v-rainbow>{{blog.title | changeUpp}}</h3>
            </router-link>
            <article>
                {{blog.content | snippet}}
            </article>
        </div>
    </div>
</template>
<script>
export default {
    name : "show-blogs",
    data () {
        return {
            blogs : [],
            search : ""
        }
    },
    //局部自定义指令
    directives : {
        // v-rainbow
        rainbow : {
            bind(el,binding,vnote) {
                el.style.color = "#" + Math.random().toString(16).slice(2,8);
                if(binding.arg == "changeBackColor") {
                    el.style.background = "#" + Math.random().toString(16).slice(2,8);
                }
            }
        }
    },
    // 局部自定义过滤器
    filters : {
        changeUpp (value) {
            return value.toUpperCase();
        }
    },
    computed : {
        filteredBlogs () {
            // 根据搜索的内容筛选对应的数据，筛选出来的数据来渲染页面有几个博客内容显示
            let filteredBlogArr = [];
            filteredBlogArr =  this.blogs.filter( (blog) => {
                return blog.title.match(this.search);
            });
            // console.log(filteredBlogArr);
            return filteredBlogArr;
        }
    },
    created () {
        // 页面刷新之前拿到数据，用来渲染single-blog

        // 拿到野狗云里面的数据
        let wilddogData = this.$http.get  ("https://wd5316696857vhtgfi.wilddogio.com/posts.json").then( (response) => {
             return response.json(); //把数据转换为promise对象,return promise对象
        })
        wilddogData.then(function (data) {  //调用promise上的then方法，里面有两个函数,then(function () {对象成功执行的函数},function () {对象失败执行的函数})

            // 把拿到的这个对象转换为数组
            var blogsArr = [];
            for(let key in data) {
                console.log(key);  // key值是独一无二的,把它的key值当做路由参数里面的id值
                data[key].id = key;
                blogsArr.push(data[key]);
            }
            // console.log(blogsArr);
            this.blogs = blogsArr;
            console.log(this.blogs);
        })
        
        // 拿到本地的json数据,只能放在static下面
        // this.$http.get('../../static/posts.json').then ( (response) => {
        //     this.blogs = response.body.slice(0,10)
        // })
    }
}
</script>
<style scoped>
#show-blogs{
    max-width: 800px;
    margin:0 auto;
}
#show-blogs a{
    text-decoration: none;
}
.single-blog{
    padding:20px;
    margin:20px 0;
    box-sizing: border-box;
    background: silver;
}
input[type='text'] {
    width:100%;
    padding: 8px;
    box-sizing: border-box;
}
</style>



